.video-container {
    position: relative; /* Required for absolute positioning of ReactPlayer */
    width: 100%;
    height: 100%;
    background-color: rgb(37, 37, 37);
    /* border-radius: 20px; */
    overflow: hidden; /* Prevent any potential overflow from the player */
    display: flex;
    align-items: center;
    justify-content: center;
}

.video-ele {
    width: 100%;
    height: 100%;
    border-radius: 20px;
}