.home-page {
  background-image: url('../images/galaxy-base-bg.jpeg');
  background-size: cover;
  background-repeat: no-repeat;
  background-color: rgba(0, 255, 255, 0.1); /* Optional: semi-transparent for subtle fallback */
  height: 100vh; /* Full viewport height */
}

.content {
  display: flex;
}

@media (max-width: 768px) {
  .content {
    flex-direction: column;
    overflow-y: auto;
    align-items: center;
    align-items: center;
    /* background-color: aqua; */
  }

  .videoSection {
  
    width: 90% !important;
    padding: 5px;
    height: 30vh;
    min-height: 300px;
    overflow: hidden;
  }

  .download-btn {
    padding: 10px;
    background-color: rgb(0, 128, 255);
    border-radius: 50px;
    text-align: center;
    text-decoration: none;
    color: aliceblue;
    margin-top: 5px;
  }

  .channelsSection {
    /* flex: none; */
    width: 100%;
    /* max-width: none; */
    justify-content: center;
    align-items: center;
    padding: 10px;
    height: 40% !important;
    padding: 0% !important;
  }

  /* .channelsSection2{
    justify-content: center;
    align-items: center;
  } */

  /* Reduce font sizes slightly for smaller screens */
  .videoSection h2 {
    font-size: 16px !important;
  }

  /* Ensure channel items are touch-friendly */
  .channel-item {
    padding: 15px !important;
    margin: 8px 0 !important;
  }

  .channel-info img {
    width: 35px !important;
    height: 35px !important;
  }

  .channel-info h4 {
    font-size: 16px !important;
  }
}

/* Very small mobile screens: Further optimizations */
@media (max-width: 480px) {
  .videoSection {
    height: 40vh;
    min-height: 250px;
    padding: 8px;
  }

  .channelsSection {
    padding: 8px;
  }

  .ListHeader h3 {
    font-size: 14px !important;
  }

  .channel-info {
    gap: 10px !important;
  }
}