/* Global reset to kill default top/body margins */
html, body {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.active {
    /* background-color: #4CAF50; */
    color: rgb(0, 128, 255) !important;
}

.navContainer {
    display: flex;
    justify-content: space-between; /* Or 'flex-start' for left-aligned, no side spaces */
    background-color: transparent;
    padding: 10px 20px; /* Adjusted: Keep top/bottom 10px, add side 20px for breathing room (optional) */
    margin: 0; /* Removes all margins; !important not needed with reset */
}

.navElement {
    text-decoration: none;
    color: antiquewhite;
    gap: 5px;
    font-family:Arial, Helvetica, sans-serif;
    font-size: 18px;
    font-weight: bold;
    align-items: center;
    padding: 10px;
    display: flex; /* Added: Enables flexbox layout for icon-text spacing via gap */
}

.searchContainer {
    display: flex;
    align-items: center;
    position: relative;
    width: 30%;
    background-color: rgba(255, 255, 255, 0.1); /* Subtle modern backdrop for dark theme */
    border-radius: 50px;
    padding: 5px 10px; /* Outer padding for container */
    border: 1px solid rgba(255, 255, 255, 0.2); /* Subtle border for modern depth */
    transition: box-shadow 0.3s ease; /* Smooth hover effect */
}

.searchContainer:hover {
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2); /* Modern hover glow */
}

.searchIcon {
    position: absolute;
    left: 15px; /* Position icon inside left */
    color: rgba(255, 255, 255, 0.7); /* Muted to match antiquewhite theme */
    font-size: 16px; /* Slightly smaller than nav icons */
    z-index: 1;
}

.navSearch {
    padding: 8px 10px 8px 40px; /* Left padding to clear icon space */
    font-size: 16px;
    width: 100%;
    border: none; /* Keep no inner border */
    border-radius: 40px; /* Slightly less than container for seamless fit */
    background: transparent; /* Transparent to show container bg */
    color: antiquewhite; /* Match nav text */
    outline: none; /* Remove default focus outline */
}

.navSearch::placeholder {
    color: rgba(255, 255, 255, 0.5); /* Subtle placeholder */
}

.navSearch:focus {
    background: rgba(255, 255, 255, 0.05); /* Subtle focus state */
}

.divider {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 20px;
}